.header{
    width: 100%;
    height: 4rem;
    padding: 0 10%;
    background-color:  #ffb366;
    font-size: 1.5rem;
}

.header nav{
    height: 100% ;
}

.header ul{
    height: 100%;
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
    justify-items: center;
}

.header li{
    margin: 0 1rem;
    width: auto;
}

.header a{
    color: white;
    text-decoration: none;
}

.header a:hover,
.header a:active,
.header a.active{
    color:  #ff8000;
    padding-bottom: 0.25rem;
    border-bottom: 4px solid #ff8000;
}